import React from 'react';
import styles from './users.module.css'
import userPict from '../../assets/images/images.png'
import {NavLink} from "react-router-dom";
import * as axios from "axios";

let Users =(props)=> {

    let pagesCount = Math.ceil(props.totalUsersCount / props.pageSize);
    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i)}

    return <div>
            <div>
                {pages.map(page => {
                    return <span className={props.currentPage === page && styles.boldPage}
                                 onClick={() => {
                                     props.onPageChanged(page)
                                 }}>{page}</span>
                })}
            </div>
            {
                props.users.map(u => <div key={u.id}>
            <span>
                <div> <NavLink to={'/profile/' + u.id}>
                    <img src={u.photos.small != null ? u.photos.small : userPict} className={styles.userFoto}/>
                </NavLink>
                </div>
                <div>
{u.follow
    ? <button disabled={props.followingProgress.some(id => id === u.id)} onClick={() => {
props.setFollowingProgress(true, u.id);
        axios.delete(`https://social-network.samuraijs.com/api/1.0/follow/${u.id}`, {
            withCredentials: true,
            headers: {'API-KEY': 'ad8e8c42-25e6-4a45-ac4d-daf70668b4a2'}
        })
            .then(response => {
                if (response.data.resultCode === 0) {
                    props.unfollow(u.id)
                }
                props.setFollowingProgress(false, u.id);
            })
    }}>unfollow</button>

    : <button disabled={props.followingProgress.some(id => id === u.id)} onClick={() => {
        props.setFollowingProgress(true, u.id);
        axios.post(`https://social-network.samuraijs.com/api/1.0/follow/${u.id}`, {}, {withCredentials: true,
                headers: {'API-KEY': 'ad8e8c42-25e6-4a45-ac4d-daf70668b4a2'}
        })
            .then(response => {
                if (response.data.resultCode === 0) {
                    props.follow(u.id)
                }
                props.setFollowingProgress(false, u.id);
            })

    }}>follow</button>}
            </div>
            </span>
                    <span>
                <span>
    <div>{u.name}</div>
    <div>{u.status}</div>
</span>
            <span>
                  <div>{'u.location.country'}</div>
                    <div>{'u.location.city'}</div>
                </span>
                </span>
                </div>)
            }
        </div>
    }

export default Users;